import React, { useState, useEffect } from 'react';
import axios from 'axios';

const Explorer = () => {
    const [articles, setArticles] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const API_URL = process.env.REACT_APP_API_URL || 'http://localhost:5000';

    // eslint-disable-next-line react-hooks/exhaustive-deps
    useEffect(() => {
        const fetchArticles = async () => {
            const token = localStorage.getItem('token');
            if (!token) {
                setError('No session found. Please log in.');
                setLoading(false);
                return;
            }

            try {
                const response = await axios.get(`${API_URL}/explore`, {
                    headers: { Authorization: `Bearer ${token}` },
                    timeout: 10000,
                });
                if (!response.data || !response.data.data) {
                    throw new Error('Invalid response data');
                }
                setArticles(response.data.data);
            } catch (err) {
                const errorMessage = err.response?.data?.message || err.message || 'Failed to fetch articles';
                setError(errorMessage.includes('404') ? 'Resource not found' : errorMessage);
                console.error(`[${new Date().toISOString()}] API Error:`, err.response?.data || err.message);
            } finally {
                setLoading(false);
            }
        };

        fetchArticles();
    }, []);

    if (loading) return <div className="flex items-center justify-center h-screen bg-gray-100"><p className="text-xl text-gray-600">Loading...</p></div>;
    if (error) return (
        <div className="flex items-center justify-center h-screen bg-gray-100">
            <div>
                <p className="text-xl text-red-600">{error}</p>
                <button className="mt-4 px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-600" onClick={() => window.location.reload()}>Retry</button>
            </div>
        </div>
    );

    return (
        <div className="max-w-6xl mx-auto p-6 bg-gray-100 min-h-screen">
            <h1 className="text-3xl font-bold mb-6 text-center">Article Exploration</h1>
            {articles.length === 0 ? (
                <p className="text-center text-gray-500">No articles to display.</p>
            ) : (
                <div className="overflow-x-auto">
                    <table className="w-full border-collapse bg-white shadow-md rounded-lg">
                        <thead>
                            <tr className="bg-gray-200">
                                <th className="border p-2">Article</th>
                                <th className="border p-2">Quantit√©</th>
                                <th className="border p-2">Type de Stock</th>
                                <th className="border p-2">D√©signation Type Stock</th>
                                <th className="border p-2">Valeur Stock</th>
                                <th className="border p-2">LE Produit</th>
                                <th className="border p-2">LE Document</th>
                                <th className="border p-2">LE Statut T√¢che</th>
                                <th className="border p-2">LE Statut Activit√©</th>
                                <th className="border p-2">LE Statut Pr√©l√®vement</th>
                                <th className="border p-2">LS Produit</th>
                                <th className="border p-2">LS Document</th>
                                <th className="border p-2">LS Statut Tache</th>
                                <th className="border p-2">LS Statut Activit√©</th>
                                <th className="border p-2">LS Statut Pr√©l√®vement</th>
                            </tr>
                        </thead>
                        <tbody>
                            {articles.map((article, index) => (
                                <tr key={index} className="hover:bg-gray-100">
                                    <td className="border p-2">{article.article || 'N/A'}</td>
                                    <td className="border p-2">{article.quantite || 'N/A'}</td>
                                    <td className="border p-2">{article.type_stock || 'N/A'}</td>
                                    <td className="border p-2">{article.designation_type_stock || 'N/A'}</td>
                                    <td className="border p-2">{article.valeur_stock || 'N/A'}</td>
                                    <td className="border p-2">{article.Produit || 'N/A'}</td>
                                    <td className="border p-2">{article.Document || 'N/A'}</td>
                                    <td className="border p-2">{article.Statut_tache_magasin || 'N/A'}</td>
                                    <td className="border p-2">{article.le_statut_activite || 'N/A'}</td>
                                    <td className="border p-2">{article.le_statut_prelevement || 'N/A'}</td>
                                    <td className="border p-2">{article.ls_produit || 'N/A'}</td>
                                    <td className="border p-2">{article.ls_document || 'N/A'}</td>
                                    <td className="border p-2">{article.ls_statut_tache || 'N/A'}</td>
                                    <td className="border p-2">{article.ls_statut_activite || 'N/A'}</td>
                                    <td className="border p-2">{article.ls_statut_prelevement || 'N/A'}</td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                </div>
            )}
        </div>
    );
};

export default Explorer;